import refreshButton from "../../assets/img/refreshButton.svg";
import React, {FC} from "react";
import addButton from "../../assets/img/addButton.svg";

interface AddButtonProps {
    onCLick: () => void,
}
export const AddButton: FC<AddButtonProps> = ({onCLick}) => {
    return (
        <button onClick={onCLick} className="page__controls-add" id="openAddManageCredentialsPopupButton">
            <img alt="Добавить" src={addButton}/>
        </button>
    )
}