import refreshButton from "../../assets/img/refreshButton.svg";
import React, {FC} from "react";
interface RefreshButtonProps {
    isActive: boolean,
    onClick: () => void,
}
export const RefreshButton: FC<RefreshButtonProps> = ({isActive, onClick}) => {
    return (
        <button onClick={onClick} className={isActive ? "page__controls-refresh active" : "page__controls-refresh"} id="refreshButton">
            <img alt="Обновить" src={refreshButton}/>
        </button>
    )
}