import React, {FC, useEffect, useState} from "react";
import {DashboardLayout} from "../../layouts/dashboardLayout";
import {RefreshButton} from "../../components/pageControls/refreshButton";
import {AddButton} from "../../components/pageControls/addButton";
import {ExportButton} from "../../components/pageControls/exportButton";
import {ManageCredentialsService} from "../../services/data/manageCredentials.service";
import {IManageCredentialsGet} from "../../store/data/manageCredentials/manageCredentials.service.interface";
import {ETooltipClass, Table} from "../../utils/tableCreator";
import {DeleteManageCredentialsPopup} from "../../components/popups/deleteManageCredentialsPopup";
import {EditButton} from "../../components/pageControls/editButton";
import {DeleteButton} from "../../components/pageControls/deleteButton";
import {useAppSelector} from "../../hooks/useAppSelector";
import {useActions} from "../../hooks/useActions";
import {AddManageCredentialsPopup} from "../../components/popups/addManageCredentialsPopup";
import {EditManageCredentialsPopup} from "../../components/popups/editManageCredentialsPopup";


interface ManageCredentialsTooltipProps {
    data: IManageCredentialsGet;
    username: string;
    isOpen: boolean;
    handleToggleTooltip: () => void;
}

export const ManageCredentialsTooltip: FC<ManageCredentialsTooltipProps> = ({
                                                                                data,
                                                                                username,
                                                                                isOpen,
                                                                                handleToggleTooltip
                                                                            }) => {
    const [editManageCredentialsIsOpen, setEditManageCredentialsIsOpen] = useState(false);
    const [deleteManageCredentialsIsOpen, setDeleteManageCredentialsIsOpen] = useState(false);

    const [tooltipContent, setTooltipContent] = useState<{ [key: string]: string; }>(
        {"": ""}
    );

    const getAdditionalData = async () => {
        const data = await ManageCredentialsService.AdditionalDataGet(username);
        setTooltipContent(data);
    };

    useEffect(() => {
        if (isOpen) {
            getAdditionalData();
        }
    }, [isOpen, username]);

    return isOpen ? (
        <>
            <tr className="tooltip">
                <td colSpan={Object.keys(data).length + 1}>
                    <div className="tooltip__content">
                        {Object.keys(tooltipContent).map((key) => (
                            <div className="tooltip__content-row" key={key}>
                                {key}: {tooltipContent[key]}
                            </div>
                        ))}
                    </div>
                    <div className="tooltip__controls">
                        <EditButton onClick={() => setEditManageCredentialsIsOpen(true)}/>
                        <DeleteButton onCLick={() => setDeleteManageCredentialsIsOpen(true)}/>
                    </div>
                </td>
            </tr>
            <EditManageCredentialsPopup data={tooltipContent} isOpen={editManageCredentialsIsOpen}
                                        setIsOpen={setEditManageCredentialsIsOpen} username={username}
                                        handleToggleTooltip={handleToggleTooltip}/>
            <DeleteManageCredentialsPopup isOpen={deleteManageCredentialsIsOpen}
                                          setIsOpen={setDeleteManageCredentialsIsOpen} username={username}/>
        </>
    ) : null;
}

export const ManageCredentials = () => {
    const [addManageCredentialsIsOpen, setAddManageCredentialsIsOpen] = useState(false);
    const {data, isLoading} = useAppSelector(state => state.manageCredentials);
    const {manageCredentialsGet} = useActions();

    useEffect(() => {
        manageCredentialsGet();
    }, [])

    return (
        <DashboardLayout>
            <div className="page__controls">
                <ExportButton url={"manageCredentials"} filename={"manageCredentials"}/>
                <div className="page__controls-right">
                    <AddButton onCLick={() => setAddManageCredentialsIsOpen(true)}/>
                    <RefreshButton isActive={isLoading} onClick={() => manageCredentialsGet()}/>
                </div>
            </div>
            <div className="table__container">
                <Table data={data} tooltipClass={ETooltipClass.MANAGE_CREDENTIALS}/>
                <div className="pagination"></div>
            </div>
            <AddManageCredentialsPopup isOpen={addManageCredentialsIsOpen}
                                       setIsOpen={setAddManageCredentialsIsOpen}/>
        </DashboardLayout>
    )
}