import { configureStore } from "@reduxjs/toolkit";
import {userReducer} from "./user/user.slice";
import {manageCredentialsReducer} from "./data/manageCredentials/manageCredentials.slice";
import {activeSessionsReducer} from "./data/activeSessions/activeSessions.slice";
import {viewSessionsReducer} from "./data/viewSessions/viewSessions.slice";

// Главный store
export const store = configureStore({
    reducer: {
        user: userReducer,
        manageCredentials: manageCredentialsReducer,
        activeSessions: activeSessionsReducer,
        viewSessions: viewSessionsReducer
    },
    // Подключение devTools
    devTools: process.env.NODE_ENV !== "production"
});
// Типизация главного store
export type RootState = ReturnType<typeof store.getState>;