import {instance} from "../../api/api.interceptors";
import {
    IViewSessionsGet,
    IViewSessionsGetSearchRequest
} from "../../store/data/viewSessions/viewSessions.service.interface";

export const ViewSessionsService = {
    async Get(data: IViewSessionsGetSearchRequest){
        const queryParams = new URLSearchParams(data as any).toString();
        const response = await instance<IViewSessionsGet[]>({
            url: `data/viewSessions?${queryParams}`,
            method: 'GET',
        });
        return response.data;
    }
}