import {IManageCredentialsGet} from "../store/data/manageCredentials/manageCredentials.service.interface";
import {IActiveSessionsGet} from "../store/data/activeSessions/activeSessions.service.interface";
import {IViewSessionsGet} from "../store/data/viewSessions/viewSessions.service.interface";
import React, {FC, memo, useState} from "react";
import {Arrow} from "../components/pageControls/arrow";
import {ManageCredentialsTooltip} from "../pages/dashboard/manageCredentials";
import {ActiveSessionsTooltip} from "../pages/dashboard/activeSessions";

const fieldNames: Record<string, string> = {};

const ignoreNames: Record<string, boolean> = {
    acctuniqueid: true
};

export enum ETooltipClass {
    MANAGE_CREDENTIALS,
    ACTIVE_SESSIONS,
    VIEW_SESSIONS
}

interface TooltipProps {
    data: IManageCredentialsGet | IActiveSessionsGet | IViewSessionsGet;
    username: string;
    isOpen: boolean;
    tooltipClass: ETooltipClass;
    handleToggleTooltip: () => void;
}

export const Tooltip: React.FC<TooltipProps> = ({data, username, isOpen, tooltipClass, handleToggleTooltip}) => {
    switch (tooltipClass) {
        case ETooltipClass.MANAGE_CREDENTIALS:
            return <ManageCredentialsTooltip data={data as IManageCredentialsGet} username={username} isOpen={isOpen}
                                             handleToggleTooltip={handleToggleTooltip}/>
        case ETooltipClass.ACTIVE_SESSIONS:
            return <ActiveSessionsTooltip data={data as IActiveSessionsGet} isOpen={isOpen}/>
        default:
            return null;
    }
};

const TableHeader: FC<{
    data: IManageCredentialsGet[] | IActiveSessionsGet[] | IViewSessionsGet[];
    tooltipClass: ETooltipClass;
}> = ({data, tooltipClass}) => {
    return (
        <thead>
        <tr>
            {Object.keys(data[0]).map((columnName, index) => (
                <React.Fragment key={index}>
                    {!ignoreNames[columnName] && (
                        <th>{fieldNames[columnName] ?? columnName}</th>
                    )}
                </React.Fragment>
            ))}
            {tooltipClass !== ETooltipClass.VIEW_SESSIONS &&
                <th></th>
            }
        </tr>
        </thead>
    );
};

interface TableRowProps {
    tooltipClass: ETooltipClass,
    item: IManageCredentialsGet | IActiveSessionsGet | IViewSessionsGet
}

const TableRow: FC<TableRowProps> = ({tooltipClass, item}) => {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <React.Fragment>
            <tr>
                {Object.entries(item).map(([key, value], index) => (
                    !ignoreNames[key] && (
                        <td key={index}>{value ?? '-'}</td>
                    )
                ))}
                {tooltipClass !== ETooltipClass.VIEW_SESSIONS &&
                    <td>
                        <Arrow handleToggleTooltip={() => setIsOpen((prevState) => !prevState)}/>
                    </td>
                }
            </tr>
            <Tooltip data={item} username={item.username} isOpen={isOpen} tooltipClass={tooltipClass}
                     handleToggleTooltip={() => setIsOpen((prevState) => !prevState)}/>
        </React.Fragment>
    );
};


interface TableProps {
    data: IManageCredentialsGet[] | IActiveSessionsGet[] | IViewSessionsGet[];
    tooltipClass: ETooltipClass
}

export const Table: React.FC<TableProps> = memo(({data, tooltipClass}) => {

    return (
        <table>
            {data.length <= 0 ? (
                <tbody>
                <tr>
                    <td colSpan={2}>Пока здесь ничего нет</td>
                </tr>
                </tbody>
            ) : <>
                <TableHeader data={data} tooltipClass={tooltipClass}/>
                <tbody>
                {data.map((item, index) => (
                        <TableRow item={item} tooltipClass={tooltipClass} key={index}/>
                    )
                )}
                </tbody>
            </>
            }
        </table>
    );
});

