import React, {FC} from "react";
import {SubmitHandler, useForm, useFormState} from "react-hook-form";
import {toast} from "react-toastify";
import {ManageCredentialsService} from "../../services/data/manageCredentials.service";
import {IManageCredentialsGet, IManageCredentialsPost} from "../../store/data/manageCredentials/manageCredentials.service.interface";
import {macAddressValidation, passwordValidation, roleValidation, usernameValidation} from "../../utils/validation";
import {TextField} from "../formComponents/textField";
import {SelectField} from "../formComponents/selectField";
import {useActions} from "../../hooks/useActions";
import {Simulate} from "react-dom/test-utils";
interface AddManageCredentialsPopupProps {
    isOpen: boolean,
    setIsOpen: (value: boolean) => void,
}

export const AddManageCredentialsPopup: FC<AddManageCredentialsPopupProps> = ({
                                                                                  isOpen,
                                                                                  setIsOpen
                                                                              }) => {
    const {manageCredentialsPost} = useActions();
    const {
        handleSubmit,
        control,
        formState: {isValid},
        reset
    } = useForm<IManageCredentialsPost>({
        defaultValues: {
            username: "",
            password: "",
            mac: "",
            role: ""
        },
        mode: "onChange"
    });

    const {errors} = useFormState({
        control
    });
    const onSubmit: SubmitHandler<IManageCredentialsPost> = addData => {
        try {
            manageCredentialsPost(addData);
            setIsOpen(false);
            reset();
        } catch (err) {
            toast.error("Ошибка. Попробуйте позже");
        }
    };
    return isOpen ? (
        <div className="popup" onClick={(event) => {
            if (event.target === event.currentTarget) {
                setIsOpen(false);
            }
        }}>
            <div className="popup__content">
                <span className="popup__close">&times;</span>
                <h2>Добавить пользователя</h2>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <TextField
                        id={"username"}
                        type={"text"}
                        name={"username"}
                        control={control}
                        error={errors.username}
                        placeholder={"Электронная почта / телефон"}
                        validation={usernameValidation}
                    />
                    <TextField
                        id={"password"}
                        type={"password"}
                        name={"password"}
                        control={control}
                        error={errors.password}
                        placeholder={"Пароль"}
                        validation={passwordValidation}
                    />
                    <TextField
                        id={"mac"}
                        control={control}
                        name={"mac"}
                        type={"text"}
                        placeholder={"MAC адрес"}
                        validation={macAddressValidation}
                        error={errors.mac}
                    />
                    <SelectField
                        id={"role"}
                        control={control}
                        name={"role"}
                        options={[
                            {value: "Administrative-User", label: "Административный пользователь"},
                            {value: "Login-User", label: "Обычный пользователь"},
                        ]}
                        validation={roleValidation}
                        error={errors.role}
                    />
                    <button type="submit" className="button-green" disabled={!isValid}>Добавить</button>
                </form>
            </div>
        </div>
    ) : null
}