const REQUIRED_FIELD = "Обязательно для заполнения";
export const usernameValidation = {
    required: REQUIRED_FIELD,
    validate: (value: string) => {
        if (value.match(/[а-яА-Я]/g)) {
            return "Русские буквы не поддерживаются";
        }
        if (
            !value.match(
                /^(\S+)@([a-z0-9-]+)(\.)([a-z]{2,4})(\.?)([a-z]{0,4})+$/g
            ) &&
            !value.match(/^\+?\d{1,4}?\s?\(?\d{2,4}\)?[\s.-]?\d{2,4}[\s.-]?\d{2,4}$/g)
        ) {
            return "Неверный формат email или телефона";
        }
        return true;
    }
};


export const passwordValidation = {
    required: REQUIRED_FIELD,
    validate: (value: string) => {
        if (value.length < 6) {
            return "Пароль должен длиннее 6-ти символов";
        }
        if (value.length > 15) {
            return "Пароль должен быть короче 15 символов";
        }
        return true;
    }
};
export const macAddressValidation = {
    required: REQUIRED_FIELD,
    validate: (value: string) => {
        const macRegex = /^([0-9A-Fa-f]{2}[:-]){5}([0-9A-Fa-f]{2})$/;
        if (!macRegex.test(value)) {
            return "Неверный формат MAC-адреса";
        }
        return true;
    },
};
export const roleValidation = {
    required: REQUIRED_FIELD
}