import {DashboardLayout} from "../../layouts/dashboardLayout";
import React, {FC, useEffect} from "react";
import {RefreshButton} from "../../components/pageControls/refreshButton";
import {ExportButton} from "../../components/pageControls/exportButton";
import {useAppSelector} from "../../hooks/useAppSelector";
import {useActions} from "../../hooks/useActions";
import {ETooltipClass, Table} from "../../utils/tableCreator";
import {IActiveSessionsGet} from "../../store/data/activeSessions/activeSessions.service.interface";
import {activeSessionsDisconnect} from "../../store/data/activeSessions/activeSessions.actions";

interface ActiveSessionsTooltipProps {
    data: IActiveSessionsGet;
    isOpen: boolean;
}

export const ActiveSessionsTooltip: FC<ActiveSessionsTooltipProps> = ({data, isOpen}) => {
    const {activeSessionsDisconnect} = useActions();
    return isOpen ? (
        <tr className="tooltip">
            <div className="tooltip__controls">
                <button style={{color: "red"}}
                        onClick={() => activeSessionsDisconnect(data.acctuniqueid)}>
                    Завершить сессию
                </button>
            </div>
        </tr>
    ) : null;
}

export const ActiveSessions = () => {

    const {data, isLoading} = useAppSelector(state => state.activeSessions);
    const {activeSessionsGet} = useActions();

    useEffect(() => {
        activeSessionsGet();
    }, [])

    return (
        <DashboardLayout>
            <div className="page__controls">
                <ExportButton filename={"activeSessions"} url={"activeSessions"}/>
                <div className="page__controls-right">
                    <RefreshButton isActive={isLoading} onClick={() => activeSessionsGet()}/>
                </div>
            </div>
            <div className="table__container">
                <Table data={data} tooltipClass={ETooltipClass.ACTIVE_SESSIONS}/>
                <div className="pagination"></div>
            </div>
        </DashboardLayout>
    )
}