import React, {FC, ReactNode, useState} from "react";
import {Link, useLocation} from "react-router-dom";
import "../styles/Header.scss"
import "../styles/Dashboard.scss"
import "../styles/Popup.scss"
interface DashboardLayoutProps {
    children: ReactNode;
}

interface INavigation {
    name: string;
    href: string;
    current: boolean;
}

const navigation: INavigation[] = [
    { name: 'Управление пользователями', href: '/dashboard/manageCredentials', current: true },
    { name: 'Активные сессии', href: '/dashboard/activeSessions', current: true },
    { name: 'Все сессии', href: '/dashboard/viewSessions', current: true },
];

export const DashboardLayout: FC<DashboardLayoutProps> = ({ children }) => {
    const location = useLocation();
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const handleMenuButtonClick = () => {
        setIsMenuOpen(!isMenuOpen);
    };

    return (
        <div>
            <header className="header">
                <div className="header__container">
                    <h2 className="header__title">Панель администратора</h2>
                    <button className="logout">Прервать сессию</button>
                </div>
            </header>
            <nav className={`sidebar ${isMenuOpen ? 'active' : ''}`}>
                <ul>
                    {navigation.map((item) => (
                        <li className={location.pathname === item.href ? 'active' : ''}>
                            <Link key={item.name} to={item.href} className={''}>
                                {item.name}
                            </Link>
                        </li>
                    ))}
                </ul>
            </nav>
            <div className="navigation__mobile">
                <button className="navigation__mobile-navigation" onClick={handleMenuButtonClick}>
                    <span className="bar"></span>
                    <span className="bar"></span>
                    <span className="bar"></span>
                </button>
            </div>
            <main className={'page'}>{children}</main>
        </div>
    );
};