import {IAuthResponse, ILoginResponse} from "./user.interface";
import {removeFromStorage} from "../../services/auth/auth.helper";
import {AuthService} from "../../services/auth/auth.service";
import {createAsyncThunk} from "@reduxjs/toolkit";
import {errorCatch} from "../../api/api.helper";
import {toast} from "react-toastify";

// Регистрация
export const admin = createAsyncThunk<IAuthResponse, ILoginResponse>(
    "account/admin",
    async (data, thunkApi) => {
        try {
            return await AuthService.Admin(data);
        } catch (err) {
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
);
// Вход
export const login = createAsyncThunk<IAuthResponse, ILoginResponse>(
    "account/login",
    async (data, thunkApi) => {
        try {
            return await AuthService.Login(data);
        } catch (err) {
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
);
// Выход
export const logout = createAsyncThunk("account/logout", async () => {
    await AuthService.Logout();
    removeFromStorage();
});
// Проверка подлинности refresh и получение обновленных данных о пользователе
export const checkAuth = createAsyncThunk<IAuthResponse>(
    "account/checkauth",
    async (_, thunkApi) => {
        try {
            return await AuthService.getNewTokens();
        } catch (err) {
            if (errorCatch(err) === "Вы не авторизованы") {
                thunkApi.dispatch(logout());
            }
            return thunkApi.rejectWithValue(err);
        }
    }
);