import {createAsyncThunk} from "@reduxjs/toolkit";
import {IActiveSessionsGet} from "./activeSessions.service.interface";
import {toast} from "react-toastify";
import {errorCatch} from "../../../api/api.helper";
import {ActiveSessionsService} from "../../../services/data/activeSessions.service";

export const activeSessionsGet = createAsyncThunk<IActiveSessionsGet[]>(
    "data/activeSessions/get",
    async (_, thunkApi) => {
        try {
            return await ActiveSessionsService.Get();
        } catch (err){
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
)
export const activeSessionsDisconnect = createAsyncThunk<string, string>(
    "data/activeSessions/delete",
    async (acctuniqueid, thunkApi) => {
        try {
            await ActiveSessionsService.Disconnect(acctuniqueid);
            return acctuniqueid;
        } catch(err){
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
)