import {createSlice} from "@reduxjs/toolkit";
import {IActiveSessionsInitialState} from "./activeSessions.service.interface";
import {activeSessionsDisconnect, activeSessionsGet} from "./activeSessions.actions";

const initialState: IActiveSessionsInitialState = {
    data: [],
    isLoading: false
}

const ActiveSessionsSlice = createSlice({
    name: "activeSessions",
    initialState,
    reducers: {},
    extraReducers: builder => {
        builder
            // Get
            .addCase(activeSessionsGet.pending, state => {
                state.isLoading = true;
            })
            .addCase(activeSessionsGet.fulfilled, (state, {payload}) => {
                state.isLoading = false;
                state.data = payload;
            })
            .addCase(activeSessionsGet.rejected, state => {
                state.isLoading = false;
                state.data = [];
            })
            // Delete
            .addCase(activeSessionsDisconnect.pending, state => {
                state.isLoading = true;
            })
            .addCase(activeSessionsDisconnect.fulfilled, (state, {payload}) => {
                state.isLoading = false;
                state.data = state.data ? state.data.filter(item => item.acctuniqueid !== payload) : [];
            })
            .addCase(activeSessionsDisconnect.rejected, state => {
                state.isLoading = false;
            })
    }
})

export const {reducer: activeSessionsReducer, actions: ActiveSessionsActions} = ActiveSessionsSlice;