import { FC, useEffect, useState } from "react";
import { useForm, Controller } from "react-hook-form";
import { useActions } from "../../hooks/useActions";
import { IManageCredentialsPatch } from "../../store/data/manageCredentials/manageCredentials.service.interface";

interface EditManageCredentialsPopupProps {
  isOpen: boolean;
  setIsOpen: (value: boolean) => void;
  data: Record<string, string>;
  username: string;
  handleToggleTooltip: () => void;
}

interface ICredentialsEntry {
  key: string;
  value: string;
}

type CredentialsFormValues = {
  [key: string]: string;
};

export const EditManageCredentialsPopup: FC<EditManageCredentialsPopupProps> =
  ({ data, isOpen, setIsOpen, username, handleToggleTooltip }) => {
    const [credentials, setCredentials] = useState<ICredentialsEntry[]>([]);
    const { manageCredentialsPatch } = useActions();
    const { control, register, handleSubmit, reset } = useForm<CredentialsFormValues>();

    useEffect(() => {
      const creds = Object.entries(data).map(([key, value]) => ({ key, value }));
      setCredentials(creds);
    }, [data]);

    const onSubmit = (formValues: CredentialsFormValues) => {
      const radcheck: Record<string, string> = {};
      const macs: Record<string, string> = {};
      credentials.forEach((credential) => {
        if (credential.key === "Cleartext-Password") {
          radcheck["Cleartext-Password"] = credential.value;
        } else if (credential.key === "MAC") {
          macs[username] = credential.value;
        } else {
          radcheck[credential.key] = credential.value;
        }
      });
      const patchData: IManageCredentialsPatch = { username, radcheck, macs };
      manageCredentialsPatch(patchData);
      setIsOpen(false);
      handleToggleTooltip();
      reset();
    };

    const handleAddCredentialClick = () => {
      setCredentials((prevCredentials) => [...prevCredentials, { key: "", value: "" }]);
    };

    const handleRemoveCredentialClick = (index: number) => {
      setCredentials((prevCredentials) => [
        ...prevCredentials.slice(0, index),
        ...prevCredentials.slice(index + 1),
      ]);
    };

    const handleCredentialChange = (index: number, field: "key" | "value", value: string) => {
      setCredentials((prevCredentials) => [
        ...prevCredentials.slice(0, index),
        { ...prevCredentials[index], [field]: value },
        ...prevCredentials.slice(index + 1),
      ]);
    };

    return isOpen ? (
      <div
        className="popup"
        onClick={(event) => {
          if (event.target === event.currentTarget) {
            setIsOpen(false);
          }
        }}
      >
        <div className="popup__content">
          <h2>Изменить пользователя</h2>
          <form onSubmit={handleSubmit(onSubmit)}>
            {credentials.map((credential, index) => (
              <div key={index} className="key-value-pair">
                {credential.key !== "Cleartext-Password" && credential.key !== "MAC" && credential.key !== "Service-Type" ? (
                  <>
                    <input
                      type="text"
                      {...register(`${credential.key}_key`)}
                      defaultValue={credential.key}
                      onChange={(event) => handleCredentialChange(index, "key", event.target.value)}
                    />
                    <input
                      type="text"
                      {...register(`${credential.key}_value`)}
                      defaultValue={credential.value}
                      onChange={(event) =>
                        handleCredentialChange(index, "value", event.target.value)
                      }
                    />
                  </>
                ) : (
                  <>
                    <Controller
                      name={`${credential.key}_key`}
                      control={control}
                      defaultValue={credential.key}
                      render={({ field }) => <input type="text" {...field} disabled />}
                    />
                    <Controller
                      name={`${credential.key}_value`}
                      control={control}
                      defaultValue={credential.value}
                      render={({ field }) => (
                        <input type="text" {...field} onChange={(event) => {
                          handleCredentialChange(index, "value", event.target.value);
                          field.onChange(event);
                        }}/>
                      )}
                    />
                  </>
                )}
                {credential.key !== "Cleartext-Password" &&
                  credential.key !== "MAC" && credential.key !== "Service-Type" && (
                    <button type="button" onClick={() => handleRemoveCredentialClick(index)}>
                      ❌
                    </button>
                  )}
              </div>
            ))}
            <div className="buttons-container">
              <button type="submit" className="button-blue">
                Сохранить
              </button>
              <button type="button" className="button-green" onClick={handleAddCredentialClick}>
                +
              </button>
            </div>
          </form>
        </div>
      </div>
    ) : null;
  };