import {createAsyncThunk} from "@reduxjs/toolkit";
import {ViewSessionsService} from "../../../services/data/viewSessions.service";
import {toast} from "react-toastify";
import {errorCatch} from "../../../api/api.helper";
import {IViewSessionsGet, IViewSessionsGetSearchRequest} from "./viewSessions.service.interface";

export const viewSessionsGet = createAsyncThunk<IViewSessionsGet[], IViewSessionsGetSearchRequest>(
    "data/viewSessions/get",
    async (data, thunkApi) => {
        try {
            return await ViewSessionsService.Get(data);
        } catch (err) {
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
)