import React, {FC} from "react";
import {toast} from "react-toastify";
import {ManageCredentialsService} from "../../services/data/manageCredentials.service";
import {useActions} from "../../hooks/useActions";

interface DeleteManageCredentialsPopupProps {
    isOpen: boolean,
    setIsOpen: (value: boolean) => void,
    username: string
}

export const DeleteManageCredentialsPopup: FC<DeleteManageCredentialsPopupProps> = ({
                                                                                        username,
                                                                                        isOpen,
                                                                                        setIsOpen
                                                                                    }) => {
    const {manageCredentialsDelete} = useActions();

    return isOpen ? (
        <div className="popup" onClick={(event) => {
            if (event.target === event.currentTarget) {
                setIsOpen(false);
            }
        }}>
            <div className="popup__content">
                <h2>Подтверждение</h2>
                <p>Вы уверены, что хотите удалить пользователя <strong>{username}</strong>?</p>
                <button className="button-red" onClick={() => manageCredentialsDelete(username)}>Подтвердить</button>
            </div>
        </div>
    ) : null
}