import React, {FC} from "react";
interface ArrowProps{
    handleToggleTooltip: any
}
export const Arrow: FC<ArrowProps> = ({handleToggleTooltip}) => {
    return (
        <svg
            width="24"
            height="24"
            viewBox="0 0 512 512"
            fill="none"
            className="arrow"
            onClick={handleToggleTooltip}
        >
            <path
                d="M93 175L256 338L419 175"
                stroke="black"
                strokeWidth="24"
                strokeLinecap="round"
            />
        </svg>
    )
}