import * as userActions from "../store/user/user.actions"
import * as manageCredentialsActions from "../store/data/manageCredentials/manageCredentials.actions"
import * as activeSessionsActions from "../store/data/activeSessions/activeSessions.actions"
import * as viewSessionsActions from "../store/data/viewSessions/viewSessions.actions"
import {bindActionCreators} from "@reduxjs/toolkit";
import {useDispatch} from "react-redux";
import {UserActions} from "../store/user/user.slice";
import {ManageCredentialsActions} from "../store/data/manageCredentials/manageCredentials.slice";
import {ActiveSessionsActions} from "../store/data/activeSessions/activeSessions.slice";
import {ViewSessionsActions} from "../store/data/viewSessions/viewSessions.slice";

const rootActions = {
    ...userActions,
    ...UserActions,
    ...manageCredentialsActions,
    ...ManageCredentialsActions,
    ...activeSessionsActions,
    ...ActiveSessionsActions,
    ...ViewSessionsActions,
    ...viewSessionsActions
};
// Достаем все dispatch из store(альтернатива useDispatch)
export const useActions = () => {
    const dispatch = useDispatch();
    return bindActionCreators(rootActions, dispatch);
};