import React, {FC} from "react";
import {Controller} from "react-hook-form";

interface ISelectFieldProps {
    id: string;
    control: any;
    name: string;
    options: { value: string; label: string }[];
    // eslint-disable-next-line
    validation: any;
    // eslint-disable-next-line
    error: any;
}

export const SelectField: FC<ISelectFieldProps> = ({
                                                       id,
                                                       control,
                                                       name,
                                                       options,
                                                       validation,
                                                       error,
                                                   }) => {
    return (
        <div className="input-container">
            <Controller
                control={control}
                name={name}
                rules={validation}
                render={({field}) => (
                    <select
                        id={id}
                        {...field}
                        className="login__select"
                    >
                        <option value="" disabled>
                            Выберите
                        </option>
                        {options.map((option) => (
                            <option key={option.value} value={option.value}>
                                {option.label}
                            </option>
                        ))}
                    </select>
                )}
            />
            {error && <span className="input__error">{error.message}</span>}
        </div>
    );
};
