import {IAuthResponse, ILoginResponse} from "../../store/user/user.interface";
import {getRefreshToken, saveTokensStorage} from "./auth.helper";
import {getContentType} from "../../api/api.helper";
import {instance} from "../../api/api.interceptors";
import axios from "axios";
import {useBuildQueryStringFromParams} from "../../hooks/useBuildQueryStringFromParams";

export const AuthService = {
    // Вход
    async Login(data: ILoginResponse) {
        const response = await instance<IAuthResponse>({
            url: "account/login?" + useBuildQueryStringFromParams(),
            method: "POST",
            data
        });
        if (response.data.accessToken) saveTokensStorage(response.data);
        return response.data;
    },
    async Admin(data: ILoginResponse) {
        const response = await instance<IAuthResponse>({
            url: "account/admin?" + useBuildQueryStringFromParams(),
            method: "POST",
            data
        });
        if (response.data.accessToken) saveTokensStorage(response.data);
        return response.data;
    },
    // Проверка подлинности refresh и получение обновленных данных о пользователе
    async getNewTokens() {
        const refreshToken = getRefreshToken();
        const response = await axios.post<string, { data: IAuthResponse }>(
            process.env.BACKEND_URL + "auth/refresh",
            {refreshToken},
            {
                headers: getContentType()
            }
        );
        if (response.data.accessToken) saveTokensStorage(response.data);
        return response.data;
    },
    // Выход(при выходе мы удаляем refresh токен на сервере)
    async Logout() {
        const response = await instance<void>({
            url: "account/logout",
            method: "get"
        });
        return response.data;
    }
};