import {createAsyncThunk} from "@reduxjs/toolkit";
import {
    IManageCredentialsGet,
    IManageCredentialsPatch,
    IManageCredentialsPost
} from "./manageCredentials.service.interface";
import {toast} from "react-toastify";
import {errorCatch} from "../../../api/api.helper";
import {ManageCredentialsService} from "../../../services/data/manageCredentials.service";

export const manageCredentialsGet = createAsyncThunk<IManageCredentialsGet[]>(
    "data/manageCredentials/get",
    async (_, thunkApi) => {
        try {
            return await ManageCredentialsService.Get();
        } catch (err){
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
)

export const manageCredentialsPost = createAsyncThunk<IManageCredentialsGet, IManageCredentialsPost>(
    "data/manageCredentials/post",
    async (data, thunkApi) => {
        try {
            return await ManageCredentialsService.Post(data);
        } catch (err){
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
)
export const manageCredentialsPatch = createAsyncThunk<IManageCredentialsGet, IManageCredentialsPatch>(
    "data/manageCredentials/patch",
    async (data, thunkApi) => {
        try {
            return await ManageCredentialsService.Patch(data);
        } catch (err){
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
)

export const manageCredentialsDelete = createAsyncThunk<string, string>(
    "data/manageCredentials/delete",
    async (username, thunkApi) => {
        try {
            await ManageCredentialsService.Delete(username);
            return username;
        } catch (err){
            toast.error(errorCatch(err));
            return thunkApi.rejectWithValue(err);
        }
    }
)