import {FC, ReactNode} from "react";
import "../styles/Header.scss"
interface AuthLayoutProps {
    children: ReactNode;
}
export const AuthLayout: FC<AuthLayoutProps> = ({children}) => {
    return (
        <div>
            <header className="header">
                <div className="header__container">
                </div>
            </header>
            <main>
                {children}
            </main>
        </div>
    )
}