import {instance} from "../../api/api.interceptors";
import {IActiveSessionsGet} from "../../store/data/activeSessions/activeSessions.service.interface";

export const ActiveSessionsService = {
    async Get() {
        const response = await instance<IActiveSessionsGet[]>({
            url: "data/activeSessions",
            method: "GET"
        })
        return response.data;
    },
    async Disconnect(acctuniqueid: string){
        const response = await instance<null>({
            url: `data/activeSessions/${acctuniqueid}`,
            method: "DELETE"
        })
        return response.data;
    }
}