import {DashboardLayout} from '../../layouts/dashboardLayout';
import {ExportButton} from '../../components/pageControls/exportButton';
import {useActions} from '../../hooks/useActions';
import {useAppSelector} from '../../hooks/useAppSelector';
import {RefreshButton} from '../../components/pageControls/refreshButton';
import {useEffect, useState} from 'react';
import {IViewSessionsGetSearchRequest} from '../../store/data/viewSessions/viewSessions.service.interface';
import {ETooltipClass, Table} from '../../utils/tableCreator';

export const ViewSessions = () => {
    const {data, isLoading} = useAppSelector((state) => state.viewSessions);
    const {viewSessionsGet} = useActions();
    const [searchParams, setSearchParams] = useState<
        IViewSessionsGetSearchRequest
    >({fromDate: '', toDate: '', username: ''});

    const handleSearchParamsChange = (field: string, value: string) => {
        setSearchParams((prevSearchParams) => ({...prevSearchParams, [field]: value}));
    };

    const handleApplyFiltersClick = () => {
        if (searchParams.fromDate.trim() !== '' || searchParams.toDate.trim() !== '' || searchParams.username.trim() !== '') {
            viewSessionsGet(searchParams);
        }
    };

    const handleClearFiltersClick = () => {
        setSearchParams({fromDate: '', toDate: '', username: ''});
        viewSessionsGet({fromDate: '', toDate: '', username: ''});
    };

    useEffect(() => {
        viewSessionsGet(searchParams);
    }, []);

    return (
        <DashboardLayout>
            <div className="page__controls">
                <ExportButton url={'viewSessions'} filename={'viewSessions'}/>
                <div className="page__controls-filter">
                    <div className="page__controls-filter__input">
                        <p>По дате от:</p>
                        <input
                            type="date"
                            id="fromDate"
                            name="fromDate"
                            value={searchParams.fromDate}
                            onChange={(e) => handleSearchParamsChange('fromDate', e.target.value)}
                        />
                    </div>
                    <div className="page__controls-filter__input">
                        <p>По дате до:</p>
                        <input
                            type="date"
                            id="toDate"
                            name="toDate"
                            value={searchParams.toDate}
                            onChange={(e) => handleSearchParamsChange('toDate', e.target.value)}
                        />
                    </div>
                    <div className="page__controls-filter__input">
                        <p>Поиск по имени</p>
                        <input
                            type="text"
                            id="username"
                            name="username"
                            value={searchParams.username}
                            onChange={(e) => handleSearchParamsChange('username', e.target.value)}
                            placeholder="Поиск"
                        />
                    </div>
                    {(searchParams.fromDate.trim() !== '' || searchParams.toDate.trim() !== '' || searchParams.username.trim() !== '') && (
                        <>
                            <button id="applyFiltersButton" onClick={handleApplyFiltersClick}>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none"
                                     stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                                     style={{backgroundColor: '#f2f7f8'}}>
                                    <circle cx="11" cy="11" r="8"></circle>
                                    <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
                                </svg>
                            </button>
                            <button id="clearFiltersButton" onClick={handleClearFiltersClick}>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none"
                                     stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                    <line x1="18" y1="6" x2="6" y2="18"></line>
                                    <line x1="6" y1="6" x2="18" y2="18"></line>
                                </svg>
                            </button>
                        </>
                    )}
                </div>
                <div className="page__controls-right">
                    <RefreshButton isActive={isLoading} onClick={() => viewSessionsGet(searchParams)}/>
                </div>
            </div>
            <div className="table__container">
                <Table data={data} tooltipClass={ETooltipClass.VIEW_SESSIONS}/>
                <div className="pagination"></div>
            </div>
        </DashboardLayout>
    );
};