import Cookies from 'js-cookie';

export const useBuildQueryStringFromParams = () => {
    const params = new URLSearchParams();
    params.append('client-ip', Cookies.get('ip') || '');
    params.append('client-mac', Cookies.get('mac') || '');
    params.append('ap-ip', Cookies.get('uamip') || '');
    params.append('ap-mac', Cookies.get('uamip') || '');
    params.append('ac-ip', Cookies.get('ac_ip') || '');
    params.append('ac-mac', Cookies.get('ac_mac') || '');
    params.append('ssid', Cookies.get('ssid') || '');
    params.append('accept-callback', Cookies.get('accept_callback') || '');
    params.append('redirect-url', Cookies.get('userurl') || '');

    return params.toString();
}
