import React, {FC} from "react";
import exportButton from "../../assets/img/exportButton.svg";
import {ManageCredentialsService} from "../../services/data/manageCredentials.service";
import {toast} from "react-toastify";

interface ExportButtonProps {
    url: string,
    filename: string
}

const handleExport = async (url: string, filename: string) => {
    try {
        await ManageCredentialsService.Export(url, filename);
    } catch (error: any) {
        toast(error.message ?? "Ошибка экспорта данных")
    }
};

export const ExportButton: FC<ExportButtonProps> = ({url, filename}) => {
    return (
        <button
            onClick={() => handleExport(url, filename)}
            className="page__controls-export"
            id="exportJSONManageCredentialsButton"
        >
            <img alt="Экспортировать" src={exportButton}/>
        </button>
    );
};