import Cookies from 'js-cookie';

export const useSaveQueryStringFromParams = () => {
    const urlParams = new URLSearchParams(window.location.search);

    if (urlParams.has('res')) {
        Cookies.set('ip', urlParams.get('ip') || '');
        Cookies.set('mac', urlParams.get('mac') || '');
        Cookies.set('uamip', urlParams.get('uamip') || '');
        Cookies.set('ac_ip', urlParams.get('ac_ip') || '');
        Cookies.set('ac_mac', urlParams.get('ac_mac') || '');
        Cookies.set('ssid', urlParams.get('ssid') || '');
        Cookies.set('accept_callback', urlParams.get('accept_callback') || '');
        Cookies.set('userurl', urlParams.get('userurl') || '');
    }
};
