import {
    IManageCredentialsGet, IManageCredentialsPatch,
    IManageCredentialsPost
} from "../../store/data/manageCredentials/manageCredentials.service.interface";
import {instance} from "../../api/api.interceptors";
import {formToJSON} from "axios";

export const ManageCredentialsService = {
    async Get(){
        const response = await instance<IManageCredentialsGet[]>({
            url: "data/manageCredentials",
            method: "GET"
        })
        return response.data;
    },
    async AdditionalDataGet(username: string){
        const response = await instance<Record<string, string>>({
            url: `data/manageCredentials/${username}`,
            method: "GET"
        })
        return response.data;
    },
    async Post(data: IManageCredentialsPost){
        const response = await instance<IManageCredentialsGet>({
            url: "data/manageCredentials",
            method: "POST",
            data
        })
        return response.data;
    },
    async Patch(data: IManageCredentialsPatch){
        const response = await instance<IManageCredentialsGet>({
            url: `data/manageCredentials/${data.username}`,
            method: "PATCH",
            data
        })
        return response.data
    },
    async Delete(username: string){
    const response  = await instance<null>({
        url: `data/manageCredentials/${username}`,
        method: "DELETE",
    })
    return response.data
},
async Export(url: string, filename: string) {
    const response = await instance.get<object>(`data/${url}/export`);
    const jsonData = JSON.stringify(response.data);
    console.log(jsonData);
    const blob = new Blob([jsonData], { type: 'application/json' });
    const exportUrl = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = exportUrl;
    a.download = filename;
    document.body.appendChild(a);
    a.click();

    URL.revokeObjectURL(exportUrl);
    document.body.removeChild(a);
}
}