import {Link, useNavigate} from "react-router-dom";
import GoogleOauthImage from "../assets/img/btnGoogleSigninDarkPressedWeb.png"
import {AuthLayout} from "../layouts/authLayout";
import "../styles/Login.scss"
import {SubmitHandler, useForm, useFormState} from "react-hook-form";
import {toast} from "react-toastify";
import {TextField} from "../components/formComponents/textField";
import {passwordValidation, usernameValidation} from "../utils/validation";
import {ILoginResponse} from "../store/user/user.interface";
import {useActions} from "../hooks/useActions";
import {useAuth} from "../hooks/useAuth";
import {useAuthRedirect} from "../hooks/useAuthRedirect";
import {useSaveQueryStringFromParams} from "../hooks/useSaveQueryStringFromParams";
import {useBuildQueryStringFromParams} from "../hooks/useBuildQueryStringFromParams";

export const Admin = () => {
    const {admin} = useActions();
    const {isLoading} = useAuth();
    const navigation = useNavigate();

    useSaveQueryStringFromParams();
    useAuthRedirect();

    const {
        handleSubmit,
        control,
        formState: {isValid}
    } = useForm<ILoginResponse>({
        defaultValues: {
            username: "",
            password: ""
        },
        mode: "onChange"
    });
    const {errors} = useFormState({
        control
    });
    const onSubmit: SubmitHandler<ILoginResponse> = async adminData => {
        try {
            await admin(adminData);
            navigation("/dasboard/manageCredentials");
        } catch (err) {
            toast.error("Ошибка. Попробуйте позже");
        }
    };
    return (
        <AuthLayout>
            <div className="container__form">
                <h2 className="admin__title">Администратор</h2>
                <form id="adminForm" className="admin__form" onSubmit={handleSubmit(onSubmit)}>
                    <TextField
                        id={"username"}
                        type={"text"}
                        name={"username"}
                        control={control}
                        error={errors}
                        placeholder={"Электронная почта / телефон"}
                        validation={usernameValidation}
                    />

                    <TextField
                        id={"password"}
                        type={"password"}
                        name={"password"}
                        control={control}
                        error={errors}
                        placeholder={"Пароль"}
                        validation={passwordValidation}
                    />
                    <button type="submit" className="button-blue admin__button">Войти</button>
                    <div className="oauth">
                        <a id="google-auth-link" href="#" className="oauth-google">
                            <img className="btn-auth-img" src={GoogleOauthImage}
                                 alt="google sign in"/>
                        </a>
                    </div>
                </form>
                <p className="login-message">Войти как <Link className="login-link"
                                                             to={`/login?${useBuildQueryStringFromParams()}`}>пользователь</Link></p>
            </div>
        </AuthLayout>
    )
}