import {IManageCredentialsInitialState} from "./manageCredentials.service.interface";
import {createSlice} from "@reduxjs/toolkit";
import {
    manageCredentialsDelete,
    manageCredentialsGet,
    manageCredentialsPatch,
    manageCredentialsPost
} from "./manageCredentials.actions";

const initialState: IManageCredentialsInitialState ={
    data: [],
    isLoading: false
}

const ManageCredentialsSlice = createSlice({
    name: "manageCredentials",
    initialState,
    reducers: {},
    extraReducers: builder => {
        builder
            // Get
            .addCase(manageCredentialsGet.pending, state => {
                state.isLoading = true;
            })
            .addCase(manageCredentialsGet.fulfilled, (state, {payload}) => {
                state.isLoading = false;
                state.data = payload;
            })
            .addCase(manageCredentialsGet.rejected, state => {
                state.isLoading = false;
                state.data = [];
            })
            // Post
            .addCase(manageCredentialsPost.pending, state => {
                state.isLoading = true;
            })
            .addCase(manageCredentialsPost.fulfilled, (state, {payload}) => {
                state.isLoading = false;
                state.data = [...state.data, payload];
            })
            .addCase(manageCredentialsPost.rejected, state => {
                state.isLoading = false;
            })
            // Patch
            .addCase(manageCredentialsPatch.pending, state => {
                state.isLoading = true;
            })
            .addCase(manageCredentialsPatch.fulfilled, (state, { payload }) => {
                state.isLoading = false;
                state.data = state.data ? state.data.map(item =>
                    item.username === payload.username ? payload : item
                ) : [payload];
            })
            .addCase(manageCredentialsPatch.rejected, state => {
                state.isLoading = false;
            })
            // Delete
            .addCase(manageCredentialsDelete.pending, state => {
                state.isLoading = true;
            })
            .addCase(manageCredentialsDelete.fulfilled, (state, { payload }) => {
                state.isLoading = false;
                // Фильтруем state.data, оставляя только элементы, у которых имя пользователя не совпадает с payload
                state.data = state.data ? state.data.filter(item => item.username !== payload) : [];
            })
            .addCase(manageCredentialsDelete.rejected, state => {
                state.isLoading = false;
            })
    }
});

export const {reducer: manageCredentialsReducer, actions: ManageCredentialsActions} = ManageCredentialsSlice;