import {Route, Routes} from "react-router-dom";
import React from "react";
import {Profile} from "../pages/profile";
import {Login} from "../pages/login";
import {Admin} from "../pages/admin";
import {ViewSessions} from "../pages/dashboard/viewSessions";
import {ActiveSessions} from "../pages/dashboard/activeSessions";
import {ManageCredentials} from "../pages/dashboard/manageCredentials";
import {PageNotFound} from "../pages/pageNotFound";

export const Navigation = () => {
    return (
        <Routes>
            <Route path={"/"} element={<Profile/>}/>
            <Route path={"/login"} element={<Login/>}/>
            <Route path={"/admin"} element={<Admin/>}/>
            <Route path={"/dashboard/viewSessions"} element={<ViewSessions/>}/>
            <Route path={"/dashboard/activeSessions"} element={<ActiveSessions/>}/>
            <Route path={"/dashboard/manageCredentials"} element={<ManageCredentials/>}/>
            {/*<Route path={"/*"} element={<PageNotFound/>}/>*/}
        </Routes>
    )
}