import {AuthLayout} from "../layouts/authLayout";
import "../styles/Profile.scss"

export const Profile = () => {
    return (
        <AuthLayout>
            <div className="container__form">
                <div id="authenticated-block" className="authenticated-block">
                    <h2 className="authenticated-block__title">Аутентифицированный пользователь</h2>
                    <p className="authenticated-block__text">Вы вошли в систему как <span></span></p>
                    <p className="authenticated-block__text">Время сеанса: <span></span> секунды
                    </p>
                    <button className="authenticated-block__button button-red">Выйти</button>
                </div>
            </div>
        </AuthLayout>
    )
}