import {IViewSessionsInitialState} from "./viewSessions.service.interface";
import {createSlice} from "@reduxjs/toolkit";
import {viewSessionsGet} from "./viewSessions.actions";

const initialState: IViewSessionsInitialState = {
    data: [],
    isLoading: false
}

const ViewSessionsSlice = createSlice({
    name: "viewSessions",
    initialState,
    reducers: {},
    extraReducers: builder => {
        builder
            .addCase(viewSessionsGet.pending, state => {
                state.isLoading = true;
            })
            .addCase(viewSessionsGet.fulfilled, (state, {payload}) => {
                state.isLoading = false;
                state.data = payload;
            })
            .addCase(viewSessionsGet.rejected, state => {
                state.isLoading = false;
                state.data = [];
            })
    }
})

export const {reducer: viewSessionsReducer, actions: ViewSessionsActions} = ViewSessionsSlice;